import css from "styled-jsx/css";
// eslint-disable-next-line import/no-anonymous-default-export
export default (props: any) => {
  console.log(props);
  return css.resolve`
    /* html标签元素样式 */
    :global(.test0) {
      color: red;
      :global(.test1) {
        color: blue;
        margin: 0 25px;
        transition: margin 2s ease-in;
      }
    }

    /* 自定义组件 */
    :global(.test2) {
      color: red;
      /* 逻辑处理 */
      background-color: ${props};
      /* background-color: #999; */
      /* animation: test2 3s ease-in infinite; */
    }
    @keyframes test2 {
      from {
        background-color: yellow;
      }
      to {
        background-color: gray;
      }
    }
    /* 自定义组件 */
    :global(.test3) {
      margin: 0 30px;
      margin-top: 30px;
      /* 组件内元素更改样式 */
      :global(.ant-card-head-title) {
        color: rosybrown;
      }
      :global(.ant-btn) {
        color: pink;
      }
    }
  `;
};
